import { data } from '../data.js';

function Card(array = data) {
    
    let template = '';
    
    array.forEach(({ id, title, tag, description }, index) => {

        let duration = index * 200 + 400;

        let img;

        let classTag = tag === 'Meat' ? 'bg-danger' : tag === 'Fruit' ? 'bg-warning' : tag === 'Vegetable' ? 'bg-success' : 'bg-info';

        if (id == 1) {
            img = require('../images/apple.jpg');
            // console.log(img)
        } else if (id == 2) {
            img = require('../images/aubergine.jpg');
        } else if (id == 3) {
            img = require('../images/banana.jpg');
        } else if (id == 4) {
            img = require('../images/brocoli.jpg');
        } else if (id == 5) {
            img = require('../images/chicken.jpg');
        } else if (id == 6) {
            img = require('../images/orange.jpg');
        } else if (id == 7) {
            img = require('../images/beef.jpg');
        } else if (id == 8) {
            img = require('../images/zucchini.jpg');
        }
        template += `
            <div class="col-12 col-md-6 col-lg-4 col-xl-3 mb-5" data-aos="fade-up-right" data-aos-duration=${duration}>
                <div class="card shadow rounded-lg">
                    <img src=${img} class="card-img-top" alt=${title}>
                    <div class="card-body">
                        <div class="d-flex justify-content-between my-2">
                            <h5 class="card-title">${title}</h5>
                            <p class="tag ${classTag}">${tag}</p>
                        </div>
                        <p class="card-text text-justify font-weight-light">${description}</p>
                        <a href="#">More info</a>
                    </div>
                </div>
            </div>
        `;
    });
    return template;
}
export default Card;