
// item = { id, text, color };

function Inputs({id, text, color}){

    let template = `
    <div class="bg-${color} d-inline px-3 py-1 mr-3 rounded-pill">
    <input type="checkbox" id=${id} name=${text} value=${text}>
    <label for=${id} class="text-white">${text}</label>
    </div>
    `;

    return template;
}

export default Inputs;