import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.css';
import './sass/app.scss';

import Nav from './components/Nav';
import {selOptions, data} from './data.js';
import Inputs from './components/InputElems';
import Card from './components/Cards';

import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();

// DOM targets
let navElem = document.querySelector('div#navbar');

let formElem = document.forms.filter;

let divContainer = document.querySelector('div.row');

// Navbar content
navElem.innerHTML = Nav();


// Form element content
selOptions.forEach(item => {
    formElem.innerHTML += Inputs(item);
});


// Div container content
divContainer.innerHTML = Card(data); 


// filter issue by change event
let filterArray = [];

[...formElem.elements].forEach(item => {

    item.addEventListener('change', (e) => {
        // console.log(e.target.value)

        if (e.target.checked){
            filterArray.push(e.target.value);

            let newContent = [];

            for(let i of filterArray){
                let x = data.filter(card => card.tag === i);
                newContent.push(...x);
            }
            divContainer.innerHTML = Card(newContent);
        
        } else {
            filterArray = filterArray.filter(item => item !== e.target.value);

            divContainer.innerHTML = '';
            
            let newContent = [];

            for(let i of filterArray){
                let x = data.filter(card => card.tag === i);
                newContent.push(...x);
            }
            divContainer.innerHTML = Card(newContent);
        }
        //console.log(filterArray);

        if (filterArray.length === 0){
            divContainer.innerHTML = Card();
        }
    });
});

// console.log(selOptions);

